<template>
	<div class="modal fade" id="questionModal" tabindex="-1" role="dialog" aria-labelledby="questionModalTitle" aria-hidden="true">
		<div class="modal-dialog modal-lg" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="questionModalTitle">Add Question</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form 
						@submit.prevent="createQuestions"
						class="modal__form" 
						method="post">
						<div class="form-group">
							<label class="modal__label" for="option-a">Option A</label>
							<quill-editor ref="myTextEditor"
								v-model="itemData.option_a"
								:options="editorOption">
							</quill-editor>
							<div class="invalid-feedback">
								{{ errors.option_a }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="option-b">Option B</label>
							<quill-editor ref="myTextEditor"
								v-model="itemData.option_b"
								:options="editorOption">
							</quill-editor>
							<div class="invalid-feedback">
								{{ errors.option_b }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button 
						@click="clearErrors()"
						class="btn btn--md" 
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button 
						@click.prevent="createQuestions"
						class="btn btn--md btn__primary" 
						type="submit"
						name="button">
						Save Test</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import { createQuestions } from '../../utils/api/quiz';
import Loader from '../../components/Loader';
import eventHub from '../../utils/eventhub';
import form from '../../mixins/form';
import questions from '../../utils/validations/questions';
import { success, fail } from '../../utils/toast';

export default {
	name: 'AddQuestion',
	mixins: [form],
	data() {
		return {
			itemData: {
				option_a: '',
				option_b: ''
			},
			errors: {},
			loading: false,
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	components: {
		Loader,
		quillEditor
	},
	methods: {
		createQuestions() {
			this.validate(this.itemData, questions.create);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createQuestions(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.loading = false;
						this.itemData = {};
						eventHub.$emit('question-created', response.data);
						success({
							title: 'Success! A new question has been created'
						});
					}
				})
				.catch(() => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong. Pls try again.'
					});
				});
			return true;
		}
	},
	components: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>

<style scoped>
.quill-editor {
    height: 20rem;
}
.modal__footer {
	margin-top: 7em;
}
</style>
