<template>
	<div class="user-profile__notification__item">
		<div class="activity">
			<p>{{index+1}}. {{question.question}}</p>
			<div>
				<h6 class="activity__header">Answer: {{question.answer}}</h6>
				<div class="activity__item">
					<div class="activity__meta">
						<span class="icon">
							<svg width="13" height="13" fill="none" xmlns="http://www.w3.org/2000/svg">
								<path fill="none" d="M0 0h13v13H0z"/>
								<path fill-rule="evenodd" clip-rule="evenodd" d="M6.5 13a6.5 6.5 0 1 0 0-13 6.5 6.5 0 0 0 0 13zm0-3.25a3.25 3.25 0 1 0 0-6.5 3.25 3.25 0 0 0 0 6.5z" fill="#F2C94C"/>
							</svg>
						</span>
						<p class="activity__title">Option A</p>
					</div>
					<div class="activity__body">
						<div class="">
							<p class="activity__text" v-html="question.option_a"></p>
						</div>
					</div>
					
				</div>

				<div class="activity__item">
					<div class="activity__meta">
						<span class="icon">
							<svg width="13" height="13" fill="none" xmlns="http://www.w3.org/2000/svg">
								<path fill="none" d="M0 0h13v13H0z"/>
								<path fill-rule="evenodd" clip-rule="evenodd" d="M6.5 13a6.5 6.5 0 1 0 0-13 6.5 6.5 0 0 0 0 13zm0-3.25a3.25 3.25 0 1 0 0-6.5 3.25 3.25 0 0 0 0 6.5z" fill="#00A65A"/>
							</svg>
						</span>
						<p class="activity__title">Option B</p>
					</div>
					<div class="activity__body">
						<div class="">
							<p class="activity__text" v-html="question.option_b"></p>
						</div>
					</div>
				</div>
			</div>
			<div>
				<button @click="deleteQuestion" class="btn btn__danger">Delete</button>
				<router-link
					:to="{ name: 'question-detail',
					params: { id: this.$route.params.id, questionId: question.id }}"
					class="btn btn__primary">
					View Details
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
import { deleteQuestion } from '../../utils/api/quiz';
import eventHub from '../../utils/eventhub';
import { fail, success } from '../../utils/toast';

export default {
	name: 'QuestionItem',
	props: {
		question: {
			type: Object,
			default() {
				return {};
			}
		},
		index: {
			type: Number,
			default() {
				return null;
			}
		},
		id: {
			type: Number,
			default() {
				return null;
			}
		}
	},
	methods: {
		deleteQuestion() {
			deleteQuestion(this.$route.params.id, this.question.id)
				.then((response) => {
					if (response.status === 200) {
						success({
							title: 'Success! Forum has been deleted'
						});
						eventHub.$emit('question-deleted', this.question.id);
					}
				})
				.catch((error) => {
					fail({
						title: `Oops! ${error.response.data.message}`
					});
					return error;
				});
			return true;
		}
	}
};
</script>

