<template>
	<div>
		<template v-if="loadingQuestions">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Questions</h3>
				</div>

				<div class="d-flex">
					<button class="btn btn__primary" type="button" name="button"
						data-toggle="modal" data-target="#questionModal">
						<span class="btn__icon">
							<svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z" fill="#fff"/></svg>
						</span>Add Question</button>
				</div>
			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Question info</p>
						<p>Actions</p>
					</div>
					<div class="contacts__body">
						<div class="user-profile__notification">
							<template v-if="questionsLoaded && questions.length">
								<QuestionItem
									v-for="(question, index) in questions"
									:key="question.id"
									:id="Number($route.params.id)"
									:question="question"
									:index="index"/>
							</template>
							<template v-else-if="questionsLoaded">
								<p>No question created</p>
							</template>
							<template v-else>
								<p>An error occurred while loading questions, please try again.</p>
							</template>
						</div>
					</div>
				</div>
			</section>
		</template>
        <Pagination
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit"
		/>
		<AddQuestion />
	</div>
</template>

<script>
import Loader from '../../components/Loader';
import { getQuestions } from '../../utils/api/quiz';
import AddQuestion from './AddQuestion';
import QuestionItem from './QuestionItem';
import eventHub from '../../utils/eventhub';

export default {
	name: 'TestDetail',
	data() {
		return {
			loadingQuestions: true,
			questionsLoaded: false,
			questions: {},
			offset: 0,
			limit: 6,
			total: 0,
			baseUrl: `quiz/${this.$route.params.id}/detail`
		};
	},
	components: {
		AddQuestion,
		Loader,
		QuestionItem
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getQuestions(this.options);
		eventHub.$on('question-created', (payload) => {
			this.questions.unshift(payload);
		});
		eventHub.$on('question-deleted', (payload) => {
			const deletedQuestion = this.questions.find((item) => {
				return item.id === payload;
			});
			this.questions.splice(this.questions.indexOf(deletedQuestion), 1);
			return true;
		});
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getQuizzes();
		}
	},
	methods: {
		getQuestions() {
			getQuestions(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.questions = response.data.data;
						this.questionsLoaded = true;
						this.loadingQuestions = false;
					}
				})
				.catch((error) => {
					this.loadingQuestions = false;
					return error;
				});
		}
	}
};
</script>

<style lang="scss" scoped>
.user-profile__notification__item {
	display: block!important;
}
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.contacts--list__item {
    display: grid;
    grid-template-columns: 35px 1fr 120px;
}
</style>
